import Scroll from '../../base/scroll/scroll.vue'
import Step from '../../base/step/step.vue'
import Upload from '../../base/upload/upload.vue'

export default {
  name: 'getPrice1',
  components: {
    Step,
    Upload,
    Scroll
  },
  data () {
    return {
      isOk: true, // 按钮可点击
      goodsImg: [],
      msgCont: '',
      twoInfo: {}
    }
  },
  // 接口请求部分开始
  created () {
    this.$axios.post('/api/orderOperate/getBuyerTask', {
      buyerTaskRecordId: this.$route.query.buyerTaskRecordId
    }).then((res) => {
      let data = res.data
      if (data.code === '200') {
      }
    }).catch(() => {
      this.$vux.alert.show({
        title: '错误提示',
        content: '服务器错误'
      })
    })
    // 核对商品的接口
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      buyerTaskRecordId: this.$route.query.buyerTaskRecordId
    }).then((res) => {
      let data = res.data
      if (data.code === '200') {
        this.twoInfo = data.data
      }
    }).catch(() => {
      this.$vux.alert.show({
        title: '错误提示',
        content: '服务器错误'
      })
    })
    // 获取与评价的内容
    this.$axios.post('/api/orderOperate/getTaskRecordByOrderId', {
      orderId: this.$route.query.buyerTaskRecordId
    }).then((data) => {
      if (data.data.code === '200') {
        this.msgCont = data.data.data.additionalFavorText
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      } else {
        this.$vux.alert.show({
          title: '获取信息失败',
          content: data.data.message
        })
      }
    }).catch(() => {
      this.$vux.alert.show({
        title: '错误提示',
        content: '服务器错误'
      })
    })
  },
  // 接口请求部分结束
  methods: {
    next () {
      if (this.goodsImg.length === 0) {
        this.$vux.alert.show({
          title: '提示',
          content: '请上传评论截图'
        })
        return false
      }
      this.$axios.post('/api/orderOperate/backOrderSubmit', {
        buyerTaskRecordId: this.$route.query.buyerTaskRecordId,
        additionalFavorPicId: this.goodsImg
      }).then((res) => {
        if (parseInt(res.data.code) === 200) {
          this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate5' } })
        }
      }).catch(() => {
        this.$vux.alert.show({
          title: '错误提示',
          content: '提交失败,请重试'
        })
      })
    }
  }
}



